import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

// Routes
import Pages from "../views"
const { Home, Developement } = Pages

const routes = [
   {
      path: '/',
      name: 'Developement',
      component: Developement
   },
   {
      path: '/dev',
      name: 'Home',
      component: Home
   }
]

const router = new VueRouter({
   mode: 'history',
   base: process.env.BASE_URL,
   routes
})

export default router
